import { Args, Int, Mutation, Query, Resolver } from '@nestjs/graphql';
import { PrismaService } from '../helper/prisma.service';
import { Project } from '../models/project.model';

@Resolver(() => Project)
export class ProjectResolver {
  constructor(private prisma: PrismaService) {}

  /**
   * ã™ã¹ã¦ã®ãƒ—ãƒ­ã‚¸ã‚§ã‚¯ãƒˆæƒ…å ±ã‚’è¿”ã™
   */
  @Query(() => [Project])
  async allProjects() {
    return this.prisma.project.findMany({
      orderBy: {
        updatedAt: 'desc',
      },
      select: {
        id: true,
        name: true,
        description: true,
        updatedAt: true,
        createdAt: true,
        todo: {
          select: {
            id: true,
            name: true,
            progress: true,
            user: {
              select: {
                id: true,
                name: true,
                updatedAt: true,
                createdAt: true,
              },
            },
            activities: {
              select: {
                id: true,
                text: true,
                updatedAt: true,
                createdAt: true,
              },
            },
            status: true,
            updatedAt: true,
            createdAt: true,
          },
        },
      },
    });
  }

  /**
   * æ–°è¦ã§ãƒ—ãƒ­ã‚¸ã‚§ã‚¯ãƒˆã‚’ä½œæˆã—ã€ä½œæˆã—ãŸãƒ—ãƒ­ã‚¸ã‚§ã‚¯ãƒˆæƒ…å ±ã‚’è¿”ã™
   */
  @Mutation(() => Project)
  async createProject(
    @Args('name', { type: () => String, nullable: false }) name: string,
    @Args('description', { type: () => String, defaultValue: '' })
    description: string
  ) {
    return this.prisma.project.create({
      data: {
        name,
        description,
      },
      select: {
        id: true,
        name: true,
        description: true,
        updatedAt: true,
        createdAt: true,
      },
    });
  }

  /**
   * ãƒ—ãƒ­ã‚¸ã‚§ã‚¯ãƒˆåã®æ›´æ–°
   */
  @Mutation(() => Project)
  async updateProjectName(
    @Args('id', { type: () => Int, nullable: false }) id: number,
    @Args('name', { type: () => String, nullable: false }) name: string
  ) {
    return this.prisma.project.update({
      where: {
        id,
      },
      data: {
        name,
      },
    });
  }

  /**
   * ãƒ—ãƒ­ã‚¸ã‚§ã‚¯ãƒˆã®æ¦‚è¦ã‚’æ›´æ–°
   */
  @Mutation(() => Project)
  async updateProjectDescription(
    @Args('id', { type: () => Int, nullable: false }) id: number,
    @Args('description', { type: () => String, nullable: false })
    description: string
  ) {
    return this.prisma.project.update({
      where: {
        id,
      },
      data: {
        description,
      },
    });
  }
}
